import $ from "jquery";

$(".home .playlist__bottom .btn-outline").on("click", function () {
    $(".playlist.home .short").toggleClass("show");
    $(this).text($(this).text() === "" ? "  5 " : "");
});

$(".home .playlist__bottom .btn-wave").on("click", function () {
    $(".playlist.home .short").toggleClass("show");
    $(this).text($(this).text() === "" ? " " : "");
});